import Vue from 'vue'
import axios from 'axios'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lists: []
  }, // 同步修改
  mutations: {
    mutationList (state, data) {
      state.lists = data
    },
    adddata (state, data) {
      state.lists.push(data)
    }
  }, // 异步修改
  actions: {
    actionList (cantext, data) {
      axios.post('https://www.fastmock.site/mock/871210bf75e9c54707d03a09a9c4c117/testUserName/user/userList', {}).then(res => {
        console.log('action', res.data.data)
        cantext.commit('mutationList', res.data.data)
      })
    }
  },
  modules: {
    vipuser: {
      namespaced: true,
      state: {
        userdata: []
      },
      mutations: {
        getuser (state, data) {
          state.userdata.push(data)
        }
      },
      actions: {
        asyncgetuser ({ commit }) {
          axios.post('https://www.fastmock.site/mock/871210bf75e9c54707d03a09a9c4c117/testUserName/user/member', {}).then(res => {
            console.log('action2:', res.data.data)
            commit('getuser', res.data.data)
          })
        }
      },
      getters: {
      }
    }
  },
  getters: {
    getList (state) {
      console.log(state.list)
    }
  }
})
